body {
  font-family: "Oswald", sans-serif;
}

.hero {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.hero h1 {
  font-size: 5em;
  margin-top: 0;
  margin-bottom: 0.5em;
  text-shadow: 2px 2px 4px #000;
}

.hero h2 {
  font-size: 2em;
  text-shadow: 2px 2px 4px #000;
}

.hero .btn {
  /* Positioning and sizing */
  display: block;
  width: 200px;

  /* Padding and margins */
  padding: 1em;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;

  /* Text styles */
  color: white;
  text-decoration: none;
  font-size: 1.5em;

  /* Border styles */
  border: 3px solid white;
  border-radius: 20px;

  /* Background styles */
  background-color: rgba(226, 100, 17, 0.918);
}

.hero {
  /* Sizing */
  width: 100vw;
  height: 100vh;

  /* Flexbox stuff */
  display: flex;
  justify-content: center;
  align-items: center;

  /* Text styles */
  text-align: center;
  color: white;

  /* Background styles */
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5))),
    url("https://bit.ly/34g13lr");
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("https://bit.ly/34g13lr");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.container-fluid {
  text-align: center;
  background-color: #212529;
  color: white;
}

.img {
  width: 200px;
  height: 220px;
  margin-top: 16px;
  border-radius: 50%;
  border: 2px solid white;
}

.intro-header {
  margin-top: 16px;
  border-bottom: 1px solid white;
}

.intro_paragraph {
  margin: 10px;
  text-align: center;
}

.skils_header {
  color: white;
  text-align: center;
  font-weight: bold;
}

.skills {
  text-align: center;
  border: 16px solid orangered;
  background-color: #212529;
}

.skills_icons {
  font-size: 60px;
  padding: 16px;
}

.skills_icons i {
  padding: 10px;
}

@media only screen and (max-width: 1024px) {
  .skills {
    text-align: center;
    width: 100%;
  }
  .skills_icons {
    font-size: 60px;
    padding: 0;
    display: flex;
    flex-direction: column;
  }
  .intro_paragraph {
    font-size: 26px;
    text-align: center;
  }
}

@media only screen and (max-width: 320px) {
    .hero h1 {
        font-size: 2em;
    }
    .hero h2 {
        font-size: 26px;
    }
}

.product__card {
    background-color: #212529;
    width: 250px;
    margin: 50px 80px;
    box-shadow: 2px  2px 4px gray;
    justify-content: center;
 }
 
 .product-img {
    width: 250px;
    height: 200px;
    border: 2px solid white;
 }
 
 .product-name {
    color: white;
    padding: 5px;
    text-align: center;
 } 

 .product-skill {
     color: orangered;
     padding: 5px;
     text-align: center;
 }
 
 .work-container{
    display: flex;
    flex-wrap: wrap;
 }
 
 .code-btn {
    display:inline-block;
    padding: 0.35em 1.2em;
    border: 0.1em solid #FFFFFF;
    margin: 5px;
    border-radius: 0.12em;
    box-sizing: border-box;
    text-decoration: none;
    text-align:center;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    text-align: center
 }
 
 .code-btn:hover {
    color: orangered;
 } 
 
 .head-name {
    text-align: center;
    margin: 10px;
    color: orangered;
 }

 @media only screen and (max-width: 600px) {
   .product__card {
      width: 100%;
      margin: 40px;
   }
   .product-img {
      width: 100%;
   }
 }

 @media only screen and (max-width: 768px) {
    .product__card {
       display: flex;
       flex-direction: column;
       width: 100%;
    }
    .product-img {
       width: 100%;
    }
    .code-btn {
       text-align: center;
    }
 }

 @media only screen and (max-width: 1024px) {
    .product__card {
       width: 100%;
    }
    .product-img {
       width: 100%;
    }
 }
.body-container {
    font-weight: 100;
    font-size: 12px;
    line-height: 30px;
  }
  
  .container {
    max-width: 400px;
    width: 100%;
    margin: 0 auto;
    position: relative;
  }
  
  #contact input[type="text"],
  #contact input[type="email"],
  #contact input[type="tel"],
  #contact input[type="url"],
  #contact textarea,
  #contact button[type="submit"] {
    font: 400 10px/10px;
  }
  
  #contact {
    background: #F9F9F9;
    padding: 25px;
    margin: 150px 0;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  }
  
  #contact h3 {
    display: block;
    font-size: 30px;
    font-weight: 300;
    margin-bottom: 10px;
  }
  
  #contact h4 {
    margin: 5px 0 15px;
    display: block;
    font-size: 13px;
    font-weight: 400;
  }
  
  fieldset {
    border: medium none !important;
    margin: 0 0 10px;
    min-width: 100%;
    padding: 0;
    width: 100%;
  }
  
  #contact input[type="text"],
  #contact input[type="email"],
  #contact input[type="tel"],
  #contact input[type="url"],
  #contact textarea {
    width: 100%;
    border: 1px solid #ccc;
    background: #FFF;
    margin: 0 0 5px;
    padding: 10px;
  }
  

  
  #contact textarea {
    height: 100px;
    max-width: 100%;
    resize: none;
  }
  
  #contact button[type="submit"] {
    cursor: pointer;
    width: 100%;
    border: none;
    background: #212529;
    color: #FFF;
    margin: 0 0 5px;
    padding: 10px;
    font-size: 15px;
  }
  
  #contact button[type="submit"]:hover {
    background: orangered;
    -webkit-transition: background 0.3s ease-in-out;
    -webkit-transition: background-color 0.3s ease-in-out;
    transition: background-color 0.3s ease-in-out;
  }
  
  #contact button[type="submit"]:active {
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5);
  }
  
  .copyright {
    text-align: center;
  }
  
  #contact input:focus,
  #contact textarea:focus {
    outline: 0;
    border: 1px solid #aaa;
  }
  
  ::-webkit-input-placeholder {
    color: #888;
  }
  
  :-moz-placeholder {
    color: #888;
  }
  
  ::-moz-placeholder {
    color: #888;
  }
  
  :-ms-input-placeholder {
    color: #888;
  }

 
